html{
  overflow-x: hidden;
  background: #d2d2d2;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto,sans-serif;
  font-size: 15px;
  background: #d2d2d2;
}

.section{
  margin: 70px 0;
}
.githubIcon{
    -webkit-animation-name: introIcon;
            animation-name: introIcon;
    -webkit-animation-duration: 1.1s;
            animation-duration: 1.1s;
    -webkit-animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
            animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -webkit-animation-delay: .7s;
            animation-delay: .7s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    width: 50px;
    margin: 20px 0 0 17px;
}

@-webkit-keyframes introIcon {
    from { -webkit-transform: scale(.4) rotateY(180deg); transform: scale(.4) rotateY(180deg); opacity: 0;}
    to { -webkit-transform: scale(1) rotateY(0deg); transform: scale(1) rotateY(0deg); opacity: 1;}
}

@keyframes introIcon {
    from { -webkit-transform: scale(.4) rotateY(180deg); transform: scale(.4) rotateY(180deg); opacity: 0;}
    to { -webkit-transform: scale(1) rotateY(0deg); transform: scale(1) rotateY(0deg); opacity: 1;}
}
.header{
    margin: 0 0 80px;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

