@-webkit-keyframes introTitle {
    from { -webkit-transform: translateY(50px) ; transform: translateY(50px) ; opacity: 0;}
    to { -webkit-transform: translateY(0px) ; transform: translateY(0px) ; opacity: 1;}
}

@keyframes introTitle {
    from { -webkit-transform: translateY(50px) ; transform: translateY(50px) ; opacity: 0;}
    to { -webkit-transform: translateY(0px) ; transform: translateY(0px) ; opacity: 1;}
}

@-webkit-keyframes introSubtitle {
    from { -webkit-transform: translateY(-30px) ; transform: translateY(-30px) ; opacity: 0;}
    to { -webkit-transform: translateY(0px) ; transform: translateY(0px) ; opacity: 1;}
}

@keyframes introSubtitle {
    from { -webkit-transform: translateY(-30px) ; transform: translateY(-30px) ; opacity: 0;}
    to { -webkit-transform: translateY(0px) ; transform: translateY(0px) ; opacity: 1;}
}


.loader{
    background: rgba(250,250,250,.8);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
}

.loaderIcon{
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
}
.github-corner svg{
    fill:#151513; 
    color:#ffffff; 
    position: absolute; 
    top: 0; 
    border: 0; 
    right: 0;
}

.github-corner .octo-arm{
    fill: #d2d2d2;
    -webkit-transform-origin: 130px 106px;
            transform-origin: 130px 106px;
}

.github-corner .octo-body{
    fill: #d2d2d2;
    -webkit-transform-origin: 130px 106px;
            transform-origin: 130px 106px;
}

.github-corner:hover .octo-arm{
    -webkit-animation:octocat-wave 560ms ease-in-out;
            animation:octocat-wave 560ms ease-in-out
}

@-webkit-keyframes octocat-wave{
    0%,100%{-webkit-transform:rotate(0);transform:rotate(0)}
    20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)}
    40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}
}

@keyframes octocat-wave{
    0%,100%{-webkit-transform:rotate(0);transform:rotate(0)}
    20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)}
    40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}
}

@media (max-width:500px){
    .github-corner:hover .octo-arm{-webkit-animation:none;animation:none}
    .github-corner .octo-arm{-webkit-animation:octocat-wave 560ms ease-in-out;animation:octocat-wave 560ms ease-in-out}}
